* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	outline: none;
	text-size-adjust: none;
	list-style-type: none;
	text-decoration: none;
    outline: none;
    line-height: normal;
    border: none;
    background: none;
    border-radius: 0;
    box-shadow: none;
    letter-spacing: unset;
    color: inherit;
    text-size-adjust: none;
	user-select: none;
	@include g;
}

img, ul li {
	display: block;
}

body {
	color: $black;
}

.header__inner {
	max-width: 1920px;
	margin: 0 auto;
	width: 100%;
	padding: 0 40px;
}

.container {
	max-width: 1240px;
	margin: 0 auto;
	width: 100%;
	padding: 0 20px;
}

.header.elements, .offer.elements {
	&::before, &::after {
		opacity: 1;
	}
}

