@font-face {
	font-family: 'GothamPro';
	src: url('../../build/fonts/GothamPro.eot');
	src: url('../../build/fonts/GothamPro.eot?#iefix') format('embedded-opentype'),
	       url('../../build/fonts/GothamPro.ttf') format('truetype'),
	       url('../../build/fonts/GothamPro.woff') format('woff');
	font-weight: normal;
  	font-style: normal;
  	font-display: swap;
}

@font-face {
	font-family: 'GothamPro-Medium';
	src: url('../../build/fonts/GothamPro-Medium.eot');
	src: url('../../build/fonts/GothamPro-Medium.eot?#iefix') format('embedded-opentype'),
	       url('../../build/fonts/GothamPro-Medium.ttf') format('truetype'),
	       url('../../build/fonts/GothamPro-Medium.woff') format('woff');
	font-weight: normal;
  	font-style: normal;
  	font-display: swap;
}

@font-face {
	font-family: 'IgraSans';
	src: url('../../build/fonts/IgraSans.otf');
	font-weight: normal;
  	font-style: normal;
  	font-display: swap;
}

@mixin g {
	font-family: 'GothamPro', sans-serif;
}

@mixin g-m {
	font-family: 'GothamPro-Medium', sans-serif;
}

@mixin i {
	font-family: 'IgraSans', sans-serif;
}