.offer {
	padding-top: 100px;
	padding-bottom: 60px;
	background: #EBEBEB;
	position: relative;
	overflow: hidden;
	&::before {
		content: '';
		display: block;
		position: absolute;
		bottom: -70px;
		left: 560px;
		width: 400px;
		height: 305px;
		background: url('../../build/img/offer_ellipse.svg') center no-repeat / cover;
		z-index: 1;
		opacity: 0;
		transition: .4s;
	}
	&::after {
		content: '';
		display: block;
		position: absolute;
		bottom: 180px;
		right: 0;
		height: 310px;
		width: 138px;
		background: url('../../build/img/offer_ellipse_2.svg') center no-repeat / cover;
		z-index: 1;
		opacity: 0;
		transition: .4s;
	}
	.container {
		position: relative;
	}
	&-title {
		font-size: 85px;
		letter-spacing: -0.02em;
		position: relative;
		z-index: 2;
		span {
			&:nth-child(1) {
				line-height: 90px;
			}
			&:nth-child(2) {
				margin-left: 300px;
				color: $blue;
				display: block;
				line-height: 65px;
			}
		}
		&__bottom {
			display: flex;
			align-items: flex-start;
			margin-left: 300px;
			span {
				&:first-child {
					margin: 0 !important;
					line-height: 100px;
				}
				&:last-child {
					line-height: 85px;
					margin-top: 8px;
				}
			}
		}
	}
	&-statement {
		margin-top: -95px;
		margin-bottom: 145px;
		position: relative;
		z-index: 0;
		&__title {
			font-size: 25px;
			line-height: 25px;
			letter-spacing: -0.02em;
			margin-bottom: 25px;
			@include i;
		}
		&__text {
			max-width: 300px;
			font-size: 18px;
			letter-spacing: -0.02em;
			color: #242424;
			line-height: 23px;
			position: relative;
			&::before {
				content: '';
				display: block;
				position: absolute;
				bottom: -35px;
				left: 0;
				width: 100%;
				height: 1px;
				background: rgba(0,0,0,.2);
			}
			&::after {
				content: '';
				display: block;
				position: absolute;
				bottom: -35px;
				left: 0;
				width: 70px;
				height: 2px;
				background: $blue;
				animation: line 1.5s ease-out;
				animation-delay: 2s;
				transition: .2s;
			}
		}
	}
	&-list {
		display: flex;
		align-items: center;
		margin-left: -120%;
		animation: list-animation 13s linear infinite;
		animation-delay: 2.5s;
		position: relative;
		z-index: 2;
		li {
			color: $blue;
			@include g-m;
			font-size: 18px;
			line-height: 35px;
			letter-spacing: -0.02em;
			white-space: nowrap;
			&:not(:last-child) {
				margin-right: 35px;
			}
		}
	}

	.go-down {
		position: absolute;
		bottom: 60px;
		left: 20px;
		animation: arrow 3s infinite;
	}
}

.get-offer {
	min-width: 225px;
	max-width: 225px;
	height: 225px;
	width: 100%;
	background: $blue;
	border-radius: 50%;
	display: flex;
	align-items: center;
	flex-direction: column;
	margin-left: 69px;
	margin-right: 45px;
	margin-top: 28px;
	box-shadow: 0 0 5px rgba(0,0,0,.2);
	&__picture {
		margin-top: 44px;
		animation: rocket 12s infinite;
		animation-delay: 1s;
		position: relative;
		z-index: 100;
        transform: rotate(9deg);
	}
	&__link {
		font-size: 18px;
		line-height: 22px;
		text-align: center;
		letter-spacing: -0.02em;
		color: #FFFFFF;
		max-width: 160px;
		margin-top: 15px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		cursor: pointer;
		position: relative;
		span {
			line-height: 22px !important;
			margin: 0 !important;
			color: #fff !important;
			position: relative;
			&::before {
				content: '';
				position: absolute;
				display: block;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 1px;
				background: #fff;
			}
		}
	}
}

@keyframes list-animation {   
	0% {     
		margin-left: -115%;   
	}   
	100% {
		margin-left: 130%;
	}
}

@keyframes arrow {
    0%{
    	opacity: 1;
        transform: translate(0px, 0px);
    }
    60%{
        opacity: 1;
    }
    70% {
        transform: translate(0px, 28px);
    }
}

@keyframes line {   
	0% {     
		width: 0;  
	}   
	100% {
		width: 70px;
	}
}

@keyframes rocket {
    0%{
        transform: rotate(9deg);
        opacity: 1;
    }
    2% {
    	transform: rotate(-8deg);
    }
    4% {
    	transform: rotate(7deg);
    }
    6% {
    	transform: rotate(-7deg);
    }
    8% {
    	transform: rotate(6deg);
    }
    10% {
    	transform: rotate(-6deg);
    }
    12% {
    	transform: rotate(4deg);
    }
    14% {
    	transform: rotate(-4deg);
    }
    16% {
    	transform: rotate(3deg);
    }
    18% {
    	transform: rotate(-3deg);
    }
    20% {
    	transform: rotate(10deg);
    	opacity: 1;
    }
    30% {
        transform: translate(920px, -350px) rotate(13deg);
    	opacity: 0;
    }
    40% {
    	transform: translate(-920px, 400px) rotate(30deg);
    	opacity: 0;
    }
    50% {
    	opacity: 1;
    }
    60% {
    	opacity: 1;
    }
    65% {
    	transform: translate(0, 1px) rotate(1deg);
    }
    70% {
    	transform: translate(0, 3px) rotate(-1deg);
    }
    75% {
    	transform: translate(0, -3px) rotate(2deg);
    }
    80% {
    	transform: translate(0, 3px) rotate(-2deg);
    }
    85% {
    	transform: translate(0, -3px) rotate(1deg);
    }
    90% {
    	transform: translate(0, 3px) rotate(-1deg);
    }
    95% {
    	transform: translate(0, -3px) rotate(1deg);
    }
    100% {
    	transform: translate(0, 0px) rotate(9deg);
    }
}

//////////////// АДАПТИВ ////////////////

@media screen and (max-width: 1250px) {
	.offer {
		&::before, &::after {
			display: none;
		}
	}
}

@media screen and (max-width: 1200px) {
	.offer-title__top {
		span {
			&:last-child {
				margin-left: 180px;
			}
		}
	}
	.offer-title__bottom {
		margin-left: 180px;
	}
}

@media screen and (max-width: 1050px) {
	.offer-title__top {
		span {
			&:last-child {
				margin-left: 100px;
			}
		}
	}
	.offer-title__bottom {
		margin-left: 100px;
	}
}

@media screen and (max-width: 990px) {
	.offer-title__top {
		text-align: center;
		span {
			&:last-child {
				margin-left: 0px;
			}
		}
	}
	.offer-title__bottom {
		margin-left: 0px;
		display: flex;
		align-items: center;
		justify-content: center;
		span {
			&:last-child {
				margin-top: 0;
				margin-left: 20px;
			}
		}
		br {
			display: none;
		}
	}
	.get-offer {
		position: absolute;
		top: 300px;
		right: 0;
		margin: 0;
	}
	.offer-statement {
		margin-top: 60px;
	}
}

@media screen and (max-width: 768px) {
	.offer-title {
		font-size: 70px;
	}
	.offer-title__bottom {
		flex-direction: column;
	}
	.get-offer {
		top: 380px;
	}
}

@media screen and (max-width: 580px) {
	.offer-statement {
		margin-top: 300px;
	}
	.get-offer {
		top: 380px;
		right: 0;
		left: 0;
		margin: 0 auto;
	}
}

@media screen and (max-width: 465px) {
	.offer {
		padding-top: 50px;
		&-title {
			font-size: 60px;
			span {
				line-height: 60px;
				margin: 0 !important;
			}
			&__top {
				span {
					&:first-child, &:last-child {
						line-height: 70px;			
					}
				}
			}
			&__bottom {
				span {
					&:first-child, &:last-child {
						line-height: 70px;			
					}
				}
			}
		}
	}
	.get-offer {
		top: 330px;
		min-width: 180px;
		max-width: 180px;
		height: 180px;
		&__picture {
			margin-top: 30px;
		}
		&__link {
			font-size: 16px;
		}
	}
}

@media screen and (max-width: 425px) {
	.offer {
		padding-top: 20px;
		&-title {
			font-size: 45px;
		}
	}
}