.activity {
	padding-top: 115px;
	padding-bottom: 160px;
	position: relative;
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		right: 530px;
		width: 165px;
		height: 190px;
		background: url('../../build/img/activity-ellipse.svg') center no-repeat / cover;
	}
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 55px;
		right: 55px;
		width: 60px;
		height: 60px;
		background: url('../../build/img/star.svg') center no-repeat / cover;
	}
	&__title {
		font-size: 60px;
		line-height: 110%;
		letter-spacing: -0.03em;
		@include g-m;
		margin-bottom: 70px;
		max-width: 700px;
	}
	&__text {
		font-size: 30px;
		line-height: 40px;
		letter-spacing: -0.03em;
		color: #000000;
		span {
			font-size: 18px;
			line-height: 22px;
			letter-spacing: -0.02em;
			color: #1C25FB;
			position: relative;
			top: -8px;
			margin-right: 119px;
		}
	}

	.telegram {
		position: absolute;
		top: 55px;
		left: 55px;
		width: 70px;
		height: 70px;
		background: $blue;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		cursor: pointer;
		img {
			position: relative;
			top: -3px;
			right: -3px;
		}
	}
}

//////////////// АДАПТИВ ////////////////

@media screen and (max-width: 1480px) {
	.activity {
		.telegram {
			top: auto;
			bottom: 0;
			left: 20px;
		}
	}
}

@media screen and (max-width: 1250px) {
	.activity {
		&::before {
			display: none;
		}
	}
}

@media screen and (max-width: 768px) {
	.activity {
		&::after {
			display: none;
		}
		&__text {
			font-size: 24px;
		}
		.telegram {
			top: auto;
			bottom: 50px;
		}
	}
}

@media screen and (max-width: 768px) {
	.activity {
		padding-top: 50px;
		&__text {
			span {
				margin-right: 20px;
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.activity {
		&__title {
			font-size: 50px;
		}
		&__text {
			span {
				margin-right: 20px;
			}
		}
	}
}

@media screen and (max-width: 425px) {
	.activity {
		&__title {
			font-size: 38px;
		}
		&__text {
			span {
				margin-right: 20px;
				top: 0;
			}
			font-size: 20px;
		}
	}
}