.header {
	padding-top: 30px;
	padding-bottom: 30px;
	background: #EBEBEB;
	position: relative;
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 340px;
		left: 0;
		width: 90px;
		height: 180px;
		background: url('../../build/img/header_ellipse_2.svg') center no-repeat / cover;
		z-index: 1;
		opacity: 0;
		transition: .2s;
	}
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 0px;
		right: 330px;
		width: 370px;
		height: 300px;
		background: url('../../build/img/header_ellipse.svg') center no-repeat / cover;
		z-index: 1;
		opacity: 0;
		transition: .2s;
	}
	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__logo {
		display: flex;
		align-items: center;
		position: relative;
		z-index: 2;
	}
	&__contacts {
		display: flex;
		align-items: flex-end;
		flex-direction: column;
		position: relative;
		z-index: 2;
		a {
			@include g-m;
			font-size: 20px;
		}
		span {
			cursor: pointer;
			color: $blue;
		}
	}
}

.logo {
	display: flex;
	align-items: center;
	&__title {
		margin-left: 20px;
		font-size: 18px;
		@include g-m;
		span {
			display: block;
			font-size: 15px;
		}
	}
}

.main-menu {
	max-width: 640px;
	width: 100%;
	position: relative;
	z-index: 2;
	ul {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__item {
		transition: .1s;
		&:hover {
			color: $blue;
		}
		&:nth-last-child(1), &:nth-last-child(2) {
			display: none;
		}
		&:nth-last-child(1) {
			color: $blue;
		}
		&:nth-last-child(2) {
			a {
				@include g-m;
			}
		}
	}
}

.burger {
	cursor: pointer;
	display: none;
	&__bar {
		width: 28px;
		height: 3px;
		background: #000;
		transition: .2s;
		&_middle {
			margin: 5px 0;
		}
	}
}

.burger.active {
	.burger__bar_middle {
		opacity: 0;
	}
	.burger__bar_top {
		transform: translateY(9px) rotateZ(45deg);
	}
	.burger__bar_bottom {
		transform: translateY(-7px) rotateZ(-45deg);
	}
}

//////////////// АДАПТИВ ////////////////

@media screen and (max-width: 1250px) {
	.main-menu {
		opacity: 0;
		visibility: hidden;
		transition: .2s;
		position: fixed;
		top: 100px;
		right: 0;
		z-index: 100;
		max-width: 100%;
		width: 100%;
		background: #fff;
		ul {
			margin-top: 20px;
			flex-direction: column;
		}
		&__item {
			margin: 0;
			margin-bottom: 30px;
		}
	}
	.main-menu.active {
		opacity: 1;
		visibility: visible;
	}
	.header {
		&__contacts {
			margin-left: auto;
			margin-right: 42px;
		}
		&__burger {
			display: block;
		}
	}
}

@media screen and (max-width: 1250px) {
	.header {
		&::before, &::after {
			display: none;
		}
	}
}

@media screen and (max-width: 600px) {
	.main-menu {
		&__item {
			&:nth-last-child(1), &:nth-last-child(2) {
				display: block;
			}
		}
	}
	.header__contacts {
		display: none;
	}
}

@media screen and (max-width: 425px) {
	.logo__title {
		display: none;
	}
}